// Presence plugin.
import * as actionTypes from '../interface/actionTypes'
import * as actions from '../interface/actions'
import { updatePresenceRequest, watchPresenceRequest } from './requests'
// Other plugins.
import { getPlatform, getRequestInfo } from '../../auth/interface/selectors'
import { NOTIFICATION_RECEIVED } from '../../notifications/interface/actionTypes'
// Libraries.
import { put, call, select, takeEvery } from 'redux-saga/effects'
// Helpers
import BasicError, { presenceCodes } from '../../errors'

// Constants
import { ACTIVITY, STATUS } from './constants'
import { platforms } from '../../constants'

export function * presenceUpdateSaga () {
  yield takeEvery(actionTypes.UPDATE, updatePresence)
}

export function * presenceGetSaga () {
  yield takeEvery(actionTypes.GET, getPresence)
}

export function * presenceSubscribeSaga () {
  yield takeEvery(actionTypes.SUBSCRIBE, subscribePresence)
}

export function * presenceUnsubscribeSaga () {
  yield takeEvery(actionTypes.UNSUBSCRIBE, unsubscribePresence)
}

export function * presenceReceivedSaga () {
  yield takeEvery(
    action =>
      action.type === NOTIFICATION_RECEIVED &&
      action.payload.notificationMessage &&
      action.payload.notificationMessage.eventType === 'presenceWatcher',
    receivePresence
  )
}

function * updatePresence ({ payload }) {
  // Verify that the status value is a valid
  if (Object.values(STATUS).indexOf(payload.status) === -1) {
    yield put(
      actions.updatePresenceFinish({
        error: new BasicError({
          code: presenceCodes.INVALID_STATUS,
          message: 'Invalid status in presence update request.'
        })
      })
    )
    return
  }

  // Verify that the activity value is a valid
  if (Object.values(ACTIVITY).indexOf(payload.activity) === -1) {
    yield put(
      actions.updatePresenceFinish({
        error: new BasicError({
          code: presenceCodes.INVALID_ACTIVITY,
          message: 'Invalid activity in presence update request.'
        })
      })
    )
    return
  }

  const requestInfo = yield select(getRequestInfo)
  let platform = yield select(getPlatform)
  requestInfo.version = platform === platforms.UC ? 1 : requestInfo.version
  const res = yield call(updatePresenceRequest, payload, requestInfo)
  if (res instanceof Error) {
    yield put(actions.updatePresenceFinish(res))
  } else {
    yield put(actions.updatePresenceFinish(payload))
  }
}

function * getPresence ({ payload }) {
  const users = Array.isArray(payload) ? payload : [payload]
  const requestInfo = yield select(getRequestInfo)
  let platform = yield select(getPlatform)
  requestInfo.version = platform === platforms.UC ? 1 : requestInfo.version
  const res = yield call(watchPresenceRequest, users, 'get', requestInfo)
  yield put(actions.getPresenceFinish(res))
}

function * subscribePresence ({ payload }) {
  const users = Array.isArray(payload) ? payload : [payload]
  const requestInfo = yield select(getRequestInfo)
  let platform = yield select(getPlatform)
  requestInfo.version = platform === platforms.UC ? 1 : requestInfo.version
  const res = yield call(watchPresenceRequest, users, 'watch', requestInfo)
  res.presentityUserId = users
  yield put(actions.subscribePresenceFinish(res))
}

function * unsubscribePresence ({ payload }) {
  const users = Array.isArray(payload) ? payload : [payload]
  const requestInfo = yield select(getRequestInfo)
  let platform = yield select(getPlatform)
  requestInfo.version = platform === platforms.UC ? 1 : requestInfo.version
  const res = yield call(watchPresenceRequest, users, 'stopwatch', requestInfo)
  yield put(actions.unsubscribePresenceFinish(res))
}

function * receivePresence (wsAction) {
  const params = wsAction.payload.notificationMessage.presenceWatcherNotificationParams
  const presence = {
    userId: params.name,
    activity: params.activity,
    status: params.status,
    note: params.note
  }
  yield put(actions.presenceReceived(presence))
}
