// Redux-Saga
import { put } from 'redux-saga/effects'
// Events
import eventsMap from '../interface/events'
import { mapEvents } from '../../events/interface/actions'

// The interface to follow.
import { api, reducer, name } from '../interface'

// State setters.
import { update as updateConfig } from '../../config/interface/actions'

// Utilities.
import { mergeValues } from '../../common/utils'
import { logManager } from '../../logs'

// Parse and/or Validate
import { validation as v8n, parse, enums } from '../../../src/common/validation'

/**
 * selector for exposed authentication state
 */
import { getExposedState } from '../interface/selectors'

import { connectFlow, setCredentialsEntry, onSubscriptionGone, onConnectionLostEntry } from './sagas'

// Get the logger
const log = logManager.getLogger('AUTH')

/**
 * Configuration options for the Authentication feature.
 * @public
 * @name config.authentication
 * @memberof config
 * @instance
 * @param {Object} authentication Authentication configs.
 * @param {Object} authentication.server
 * @param {string} authentication.server.base Hostname of the server to be used for requests.
 * @param {string} [authentication.server.protocol='https'] Protocol to be used for requests.
 * @param {Number} [authentication.server.port=443] Port to be used for requests.
 */

const defaultOptions = {
  server: {
    protocol: 'https',
    base: null,
    port: 443,
    version: '1' // not documented, but important
  }
}

// config validation
const v8nValidation = v8n.schema({
  server: v8n.schema({
    protocol: enums(['http', 'https']),
    base: v8n.string(),
    port: v8n.positive(),
    version: enums(['1']) // not documented, but important
  })
})
const parseOptions = parse('authentication', v8nValidation)

/**
 * On link authentication implementation factory.
 * @method authLink
 * @param {Object} options - Configuration options for authentication. See above.
 * @return {Object} plugin - An authentication plugin.
 */
export default function authLink (options = {}) {
  if (options.subscription || options.websocket) {
    log.warn('Authentication configuration is being replaced, see docs. Please update to the config.')
  }

  // For backwards compatibility we check if the server is defined under subscription
  if (options.subscription) {
    if (!options.server) {
      options.server = {}
    }

    options.server.base = options.server.base || options.subscription.server
    options.server.protocol = options.server.protocol || options.subscription.protocol
    options.server.port = options.server.port || options.subscription.port
  }

  options = mergeValues(defaultOptions, options)

  parseOptions(options)

  if (!options.server.base) {
    log.error('No server configuration provided. Please provide proper authentication configurations.')
  }

  function * init () {
    // Send the provided options to the store.
    // This will be `state.config[name]`.
    yield put(updateConfig(options, name))
    yield put(mapEvents(eventsMap))
  }

  const capabilities = ['userCredentialsAuth', 'hmacTokenAuth', 'bearerAccessTokenAuth']

  return {
    sagas: [connectFlow, setCredentialsEntry, onSubscriptionGone, onConnectionLostEntry],
    capabilities,
    init,
    api,
    selector: getExposedState,
    reducer,
    name
  }
}
